@media screen and (max-width: 1100px), screen and (max-device-width: 1100px){
  body{
    .content{
      .firstBlock{
        background-image: url("../img/first_block_bg_mobie.jpg");
        .firstBlock__contacts{
          justify-content: flex-end;
          ul{
            display: none;
          }
        }
        .firstBlock__info{
          margin: 40px 0 0 30px;
          li{
            &:nth-child(1){
              margin: 0 0 0 40px;
            }
            &:nth-child(2){
              margin: 0 0 0 80px;
            }
            &:nth-child(3){
              margin: 0 0 0 120px;
            }
            &:nth-child(4){
              margin: 0 0 0 160px;
            }
            &:nth-child(5){
              margin: 0 0 0 200px;
            }
            &:nth-child(6){
              margin: 0 0 0 240px;
            }
          }
        }
        button{
          margin: 20px 0 0 550px;
        }
      }
      .secondBlock{
        background-image: url("../img/second_block_bg_mobile_01.jpg");
        .secondBlock__headline{
          margin: 60px 0 0 -10px;
        }
        .secondBlock__video{
          margin: 70px 0 0;
          width: 169%;
          height: 370px;
        }
      }
      .thirdBlock{
        background-image: url("../img/third_block_bg_mobie.jpg");
        .thirdBlock__content{
          margin: 130px 0 0;
          display: flex;
          justify-content: space-around;
          align-items: center;
          .thirdBlock__headline,
          .thirdBlock__info{
            margin: 0;
          }
        }
      }
      .fourthBlock{
        background-image: url("../img/fourth_block_bg_mobile.jpg");
        .category{
          .debutant,
          .nominee,
          .superstar{
            h6{
              font-size: 2.5em;
              line-height: 60px;
            }
          }
        }
      }
      .fifthBlock{
        background-image: url("../img/fifth_block_bg_mobile.jpg");
        .fifthBlock__headline{
          h6{
            font-size: 3.5em;
          }
          span{
            font-size: 2.125em;
          }
        }
        .fifthBlock__foto{
          ul{
            margin: 70px 0 0;
            li{
              img{
                height: 200px;
              }
            }
          }
        }
        .fifthBlock__footer{
          ul{
            li{
              text-align: center!important;
            }
          }
        }
      }
      .sixthBlock{
        background-image: url("../img/sixth_block_bg_mobile.jpg");
        .sixthBlock__headline{
          margin: 60px 0 0;
          h6{
            text-align: center;
          }
        }
        .sixthBlock__info{
          margin: 30px 0 0;
          display: flex;
          flex-direction: column;
          justify-content: space-around;
          align-items: center;
          ul,
          .adult{
            margin: 0;
          }
          .adult{
            font-size: 1.625em;
          }
        }
      }
      .seventhBlock{
        background-image: url("../img/seventh-block_bg_mobile.jpg");
        .seventhBlock__contacts{
          margin: 45px 0 0;
        }
        .seventhBlock__logo{
          margin: -15px 0 0;
        }
        .seventhBlock__map{
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          position: unset;
          background: #fff;
          border-radius: 10px;
          box-shadow: 10px 10px 20px #fff, -10px -10px 20px #fff, 10px -10px 20px #fff, -10px 10px 20px #fff;
          div{
            width: 33.3%;
            position: unset;
            text-align: center;
            ul{
              position: unset;
              li{
                &:nth-child(1){
                  p{
                    font-size: 2em;
                  }
                }
              }
              &:after{
                display: none;
              }
            }
          }
        }
        button{
          margin: 65px 0 0;
        }
        .seventhBlock__totalOrder{
          margin: 0;
        }
      }
    }
  }
}

